@keyframes rotating {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  height: 100vh;
  margin: 0;
}

body, .stopwatch {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__hand-minutes, .stopwatch__hand-seconds {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand-minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotating;
}

.stopwatch__hand-seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotating;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__hand-minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand-seconds {
  animation-duration: 10s;
}
